import Cock from '../base/cock'
import tpl from './view/class.tmpl'
import data from './data/class_cn'
import cnLang from './lang/cn'

var i, x, tmp = {},
  auto = {
    name: 'class',
    tpl: tpl,
    data: data,
    tip: '请选择行业',
    //multi: 1,
    ratio: 12,
    parent: false, // 这是父级可选
    mui: {},
  }, key;

auto.data.lang = cnLang;


auto.data.type = {};
auto.data.all = [];
for (i = 0; i < auto.data.a_ll.length; i++) {
  key = auto.data.a_ll[i];
  auto.data.type[key] = auto.data.raw[key];
  tmp[key] = i;
  auto.data.all.push([key, []]);
}


